import { css, keyframes } from "@emotion/css";

export const RouletteText = (props) => {
  const { children, start } = props;

  return (
    <p className={start == true ? resultItemmm : resultItem}>{children}</p>
  );
};
const rouletteTextAnime = keyframes`
0%{
  font-size: 28px;
}
25%{
  font-size: 24px;
}
50%{
  font-size: 28px;
}
75%{
  font-size: 24px;
}
100%{
  font-size: 28px;
}
`;
const resultItem = css`
  margin-bottom: 2.5vh;
  font-size: 28px;
  color: #ffb901;
  text-align: center;
  white-space: pre-wrap;
  @media (max-height: 740px) {
    font-size: 24px;
  }
  @media (max-height: 740px) {
    font-size: 26px;
    margin-bottom: 3vh;
  }
  animation: ${rouletteTextAnime} 1.2s;
`;
const resultItemmm = css`
  margin-bottom: 2.5vh;
  font-size: 28px;
  color: #ffb901;
  text-align: center;
  white-space: pre-wrap;
  @media (max-height: 740px) {
    font-size: 24px;
  }
  @media (max-height: 740px) {
    font-size: 26px;
    margin-bottom: 3vh;
  }
`;
